import React, { Component } from 'react';
import '../css/Menu.css';
import { Link } from 'react-router-dom';
import Dropdown from './Dropdown';
import AccountDropdown from './AccountDropdown';
import Search from './Search';
import { cookieFunctions } from '../cookieFunctions.js';

const Links = [
  {
    path: "/games",
    desc: ""
  },
  {
    path: "/movies",
    desc: ""
  },
  {
    path: "/tvshows",
    desc: ""
  },
  {
    path: "/anime",
    desc: ""
  }
];

const usrLinks = [
  {
    path: "/myAccount",
    desc: " ",
  },
  {
    path: "/settings",
    desc: "",
    user: cookieFunctions.getCookie('user'),
    userToken: cookieFunctions.getCookie('userToken')
  },

  {
      path: "/add",
      desc: " ",
      user: cookieFunctions.getCookie('user'),
      userToken: cookieFunctions.getCookie('userToken')
  }

];

class Menu extends Component{

  constructor(props){
    super(props);
    this.state = { user : document.cookie};
  }

  render(){
    let btn;
    if(cookieFunctions.getCookie('userToken') === '')
     btn = <Link to="/signin" className="usrLink"></Link>;
    else btn = <AccountDropdown classname="dropdown2" id="dd2" name="" links={usrLinks}/>;
    return(
      <div className="navbar">
        <div className="links">
          <Link to="/"></Link>
          <Dropdown classname="dropdown" id="dd1" name="" links={Links}/>
          <Link to="/users"></Link>
        </div>
      {btn}
      <Search />
      </div>

    )
  }

}

export default Menu;



